.dataTables_wrapper {position: relative;min-height: 500px;}
.dataTables_wrapper > div {
	background-color: #f9fafc;
	padding: 8px 0 5px;
	width: auto;
	border: 1px solid #eaedf1;
}

.dataTables_wrapper > div:first-child {
	border-top-width: 1px;
	border-bottom-width: 0;
}

.dataTables_wrapper .row {
	margin: 0;
}

.dataTables_wrapper .dataTables_processing {
	position: fixed;
	left: 0;width: 100%;top: 0; height: 100%;
	z-index: 99999;
	background-color: transparent;
	border: 0 none;
}

.dataTables_wrapper .dataTables_processing .mask {
	position: absolute;
	top: 50%; left: 50%; -webkit-transform: translate(-50%,-50%); transform: translate(-50%,-50%);
	width: 200px; height: 200px;

	opacity: 0.6;
	filter: alpha(opacity=60);
	z-index: 1;
	background-color: #000;
	border-radius: 35px;
}
.dataTables_wrapper .dataTables_processing .inner {
	position: absolute;
	top: 50%; left: 50%; -webkit-transform: translate(-50%,-50%); transform: translate(-50%,-50%);
    width: 150px;
    height: 170px;
    text-align: center;
	z-index: 2;
}

.dataTables_empty {text-align: center;}

.dataTables_filter label,
.dataTables_length label,
.dataTables_info,
.dataTables_paginate {
	margin: 0;
	padding: 0;
}

.dataTables_filter label {
	font-weight: normal;
	float: left;
	text-align: left;
}

div.dataTables_length select {
	width: 75px;
}

div.dataTables_filter label {
	font-weight: normal;
	float: right;
}

div.dataTables_filter input {
	width: 150px;
}

.dataTables_info {
	padding-top: 8px;
}

.dataTables_paginate {
	float: right;
	margin: 0;
	width: 100%;
}

div.dataTables_paginate ul.pagination {
	margin: 2px 0;
	white-space: nowrap;
}

div.dataTables_paginate .popover{
	min-width: 80%;
	max-width: 100%; /* Max Width of the popover (depending on the container!)*/
}

div.dataTables_paginate .popover .slider.slider-horizontal{
	width: 100%;
}

table.dataTable td,
table.dataTable th {
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
}

table.dataTable tr.active td,table.dataTable tr.active th {
	background-color: #e9e9e9;
}

table.dataTable {
	clear: both;
	margin-top: 0 !important;
	margin-bottom: 0 !important;
	max-width: none !important;
}

table.table thead .sorting,
table.table thead .sorting_asc,
table.table thead .sorting_desc,
table.table thead .sorting_asc_disabled,
table.table thead .sorting_desc_disabled {
	cursor: pointer;
	*cursor: hand;
	padding-right: 20px;
}

.table thead .sorting,
.table thead .sorting_asc,
.table thead .sorting_desc,
.table thead .sorting_asc_disabled,
.table thead .sorting_desc_disabled {
	background-position: center right;
	background-repeat: no-repeat;
	background-size: 19px 19px;
}

.table thead .sorting { background-image: url('../../js/datatable/images/sort_both.png'); }
.table thead .sorting_asc { background-image: url('../../js/datatable/images/sort_asc.png'); }
.table thead .sorting_desc { background-image: url('../../js/datatable/images/sort_desc.png'); }
.table thead .sorting_asc_disabled { background-image: url('../../js/datatable/images/sort_asc_disabled.png'); }
.table thead .sorting_desc_disabled { background-image: url('../../js/datatable/images/sort_desc_disabled.png'); }

table.dataTable thead > tr > th {
	padding-left: 18px;
	padding-right: 18px;
}

table.dataTable th:active {
	outline: none;
}

div.dataTables_scrollHead table {
	margin-bottom: 0 !important;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
}

div.dataTables_scrollHead table thead tr:last-child th:first-child,
div.dataTables_scrollHead table thead tr:last-child td:first-child {
	border-bottom-left-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
}

div.dataTables_scrollBody table {
	border-top: none;
	margin-top: 0 !important;
	margin-bottom: 0 !important;
}

div.dataTables_scrollBody tbody tr:first-child th,
div.dataTables_scrollBody tbody tr:first-child td {
	border-top: none;
}

div.dataTables_scrollFoot table {
	margin-top: 0 !important;
	border-top: none;
}

div.dataTables_scrollHead table.table-bordered {
	border-bottom-width: 0;
}

@media only screen and (-Webkit-min-device-pixel-ratio: 1.5),
only screen and (-moz-min-device-pixel-ratio: 1.5),
only screen and (-o-min-device-pixel-ratio: 3/2),
only screen and (min-device-pixel-ratio: 1.5) {

	.table thead .sorting { background-image: url('../../js/datatable/images/sort_both@2x.png'); }
	.table thead .sorting_asc { background-image: url('../../js/datatable/images/sort_asc@2x.png'); }
	.table thead .sorting_desc { background-image: url('../../js/datatable/images/sort_desc@2x.png'); }
	.table thead .sorting_asc_disabled { background-image: url('../../js/datatable/images/sort_asc_disabled@2x.png'); }
	.table thead .sorting_desc_disabled { background-image: url('../../js/datatable/images/sort_desc_disabled@2x.png'); }
}